import { ViewportScroller } from '@angular/common';
import { ChangeDetectorRef, Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Subject, takeUntil } from 'rxjs';
import { UserService } from 'src/app/core/user/user.service';
import { environment } from 'src/environments/environment';
import { AdminService } from '../../admin.service';
import { EditarTypeComponent } from '../modals/editar-type/editar-type.component';

@Component({
  selector: 'app-list-type',
  templateUrl: './list-type.component.html',
  styleUrls: ['../types-admin.component.scss']
})
export class ListTypeComponent implements OnInit, OnDestroy {

  @ViewChild("editarTypeModal") EditarModal: EditarTypeComponent;

  private _unsubscribeAll: Subject<any> = new Subject<any>();

  typesCount: number = 0;
  typesList: any = [];
  page: any = 1;
  limit: any = environment.pagination_limit;
  Toast:any;

  typeSelected:any = {};

  constructor(
    private _userService: UserService,
    private _changeDetectorRef: ChangeDetectorRef,
    private modalService: NgbModal,
    private _formBuilder: FormBuilder,
    private viewScroller: ViewportScroller,
    private _adminService: AdminService
  ) { }

  ngOnInit() {
    //Add 'implements OnInit' to the class.
    this._adminService.types$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((types: any) => {
        // Update the selected agente
        this.typesList = types;
        this.typesCount = types.length;

        // Mark for check
        this._changeDetectorRef.markForCheck();
      });
  }

  /**
  * On destroy
  */
  ngOnDestroy(): void
  {
      // Unsubscribe from all subscriptions
      this._unsubscribeAll.next(null);
      this._unsubscribeAll.complete();
  }

  abrirModalEditarType(type: any){
    this._adminService.setTypeSelected(type);
    this.EditarModal.openModal();
  }

}
