import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TypesAdminComponent } from './types-admin.component';
import { ListTypeComponent } from './list-type/list-type.component';
import { TypesListResolver } from '../admin.resolver';

const routes: Routes = [
  { 
    path: '',
    component: TypesAdminComponent,
    children:[
      {
        path: 'list',
        component: ListTypeComponent,
        resolve:{
          types: TypesListResolver,
        },
        data: {
          title: "Tipos pulsera",
          breadcrumb: "Lista"
        },
      },
    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TypesAdminRoutes { }
