import { isPlatformBrowser } from '@angular/common';
import { ChangeDetectorRef, Component, Inject, Input, OnDestroy, OnInit, PLATFORM_ID, TemplateRef, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Gallery, GalleryItem } from 'ng-gallery';
import { Lightbox } from 'ng-gallery/lightbox';
import { Subject, takeUntil } from 'rxjs';
import { AdminService } from 'src/app/admin/admin.service';
import { UserService } from 'src/app/core/user/user.service';
import { environment } from 'src/environments/environment';
import Swal from 'sweetalert2';

@Component({
  selector: "app-editar-type",
  templateUrl: "./editar-type.component.html",
  styleUrls: ["./editar-type.component.scss"],
})
export class EditarTypeComponent implements OnInit, OnDestroy {
  type: any = {};
  @ViewChild("modalEditarPulsera", { static: false }) ModalTypePulsera: TemplateRef<any>;
  private _unsubscribeAll: Subject<any> = new Subject<any>();

  public closeResult: string;
  public modalOpen: boolean = false;

  user: any = {};

  Toast: any;

  loading: boolean = true;

  acepting: boolean = true;
  limit: any = environment.pagination_limit;

  items: GalleryItem[];

  descriptionControl: FormControl = new FormControl('', [Validators.required]);

  priceControl: FormControl = new FormControl('', [Validators.required, Validators.min(1), Validators.max(100000)]);

  constructor(
    @Inject(PLATFORM_ID) private platformId: Object,
        private router: Router, private modalService: NgbModal,
        private _formBuilder: FormBuilder,
        private _userService: UserService,
        private _changeDetectorRef: ChangeDetectorRef,
        private _adminService: AdminService, public gallery: Gallery, public lightbox: Lightbox
  ) {
    this.Toast = Swal.mixin({
      toast: true,
      position: 'top-end',
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener('mouseenter', Swal.stopTimer)
        toast.addEventListener('mouseleave', Swal.resumeTimer)
      }
    });  
  }

  ngOnInit() {
    this._adminService.typeSelected$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((type: any) => {
        this.loading = true;
        this.type = type;
        this.loading = false;
        if(type?.name){
          this.descriptionControl.setValue(type.description);
          this.priceControl.setValue(type.precio);
        }
        // console.log(this.user);

        // Mark for check
        this._changeDetectorRef.markForCheck();
      });
  }

  ngOnDestroy() {
    if(this.modalOpen){
      this.modalService.dismissAll();
    }
    // Unsubscribe from all subscriptions
    this._unsubscribeAll.next(null);
    this._unsubscribeAll.complete();
  }

  openModal() {
    this.acepting = true;
    this.modalOpen = true;
    if (isPlatformBrowser(this.platformId)) {
      // For SSR
      this.modalService
        .open(this.ModalTypePulsera, {
          size: "lg",
          ariaLabelledBy: "modal-basic-title",
          centered: true,
          windowClass: "ModalTypePulsera",
          backdrop: "static",
          keyboard: false,
        })
        .result.then(
          (result) => {
            `Result ${result}`;
          },
          (reason) => {
            this.closeResult = `Dismissed`;
          }
        );
    }
  }
  

  actualizarType(){
    this.acepting = false;
    if(this.priceControl.valid && this.descriptionControl.valid){
      let data = {
        precio: this.priceControl.value,
        description: this.descriptionControl.value
      }
      this.updateTypePulsera(data);
    }else{
      this.acepting = true;
      this.Toast.fire({
        icon: 'error',
        title: 'Debe completar los campos con datos validos para guardar cambios'
      });
    }
  }

  updateTypePulsera(data:any){
    this._adminService.updateTypePulsera(this.type._id,data).pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        this.Toast.fire({
          icon: 'success',
          title: 'Tipo de pulsera actualizada con exito.'
        });
        this.updateTypesPulseraList();
      },(error) => {
        this.Toast.fire({
          icon: 'error',
          title: error.error.message
        });
      }
    );
  }

  updateTypesPulseraList(){
    this._adminService.getTypesPulserasUpdated().pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        this._adminService.updateTypesPulserasList(response);
        this.modalService.dismissAll();
      },(error) => {
        this.Toast.fire({
          icon: 'error',
          title: error.error.message
        });
      }
    );
  }
}
